import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { NewDownloadPageComponent } from './new-download-page.component';

const routes: Routes = [
  { path: '', component: NewDownloadPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class NewDownloadPageRoutingModule { }
